*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #65d2ea;
}
.container {
  width: 40%;
  min-width: 450px;
  border: 1px solid black;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  padding: 50px 30px;
}
.container * {
  font-family: 'Poppins', sans-serif;
  border: none;
  outline: none;
}
.inputs-wrapper {
  background-color: #080808;
  padding: 30px 25px;
  border-radius: 8px;
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
}
input,
button {
  height: 50px;
  background-color: #fff;
  color: #080808;
  font-weight: 500;
  border-radius: 5px;
}
input {
  width: 60%;
  padding: 0 20px;
  font-size: 14px;
}
button {
  width: 30%;
  float: right;
}
