*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #65d2ea;
}

.container {
  width: 40%;
  min-width: 450px;
  border: 1px solid #000;
  padding: 50px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container * {
  border: none;
  outline: none;
  font-family: Poppins, sans-serif;
}

.inputs-wrapper {
  background-color: #080808;
  border-radius: 8px;
  padding: 30px 25px;
  box-shadow: 0 15px 20px rgba(0, 0, 0, .3);
}

input, button {
  height: 50px;
  color: #080808;
  background-color: #fff;
  border-radius: 5px;
  font-weight: 500;
}

input {
  width: 60%;
  padding: 0 20px;
  font-size: 14px;
}

button {
  width: 30%;
  float: right;
}

